body, html {
  overflow-x: hidden; }

.navbar {
  background-color: #000;
  padding-top: 0 !important;
  padding-bottom: 0 !important; }

.navbar ul {
  list-style-type: none;
  padding-left: 0;
  margin: 0; }

.navbar ul li {
  display: inline-block; }

.navbar a {
  padding: 15px;
  font-size: 14px;
  text-decoration: none !important;
  color: #000;
  display: block;
  -webkit-transition: background-color .3s;
  transition: background-color .3s; }

.navbar li a:hover {
  color: #BADA55;
  background-color: #000; }

.navbar li a.active {
  border-bottom: 2px solid #fff; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

img {
  max-width: 100%;
  height: auto; }

.section {
  height: 800px;
  position: relative; }
  .section:not(.hero) {
    padding: 90px 0px 80px;
    height: auto; }

.section-content {
  font-size: 14px;
  font-weight: 400;
  color: #333;
  letter-spacing: 0;
  margin-bottom: 19px;
  font-family: 'Poppins', sans-serif;
  line-height: 28px; }

.hero {
  background-image: url(/myreactapp3/static/media/slider-bg.d4ad45a3.jpg);
  background-position: 50% 0px;
  background-size: cover;
  display: flex;
  align-items: center; }

.divider {
  display: none;
  position: absolute;
  left: 0;
  bottom: -2px;
  width: 100%;
  overflow: hidden; }
  .divider.svg_white svg {
    fill: #fff;
    width: 101%; }
  .divider.svg_gray svg {
    fill: #212121;
    width: 101%; }

.bg-gray {
  background-color: #212121; }

.section-title h2 {
  font-size: 29px;
  letter-spacing: 0;
  line-height: 30px;
  padding-bottom: 16px;
  position: relative;
  text-transform: uppercase;
  font-family: 'Russo One', sans-serif;
  font-weight: 700; }

.section-title-center h2 {
  text-align: center; }

.section-title-center p {
  text-align: center;
  margin-bottom: 40px;
  margin-top: 5px;
  font-weight: 300;
  color: #b9b9b9;
  letter-spacing: 0.04em;
  font-size: 30px;
  text-transform: lowercase; }

.section-title-center h2:after {
  margin: 0 auto; }

.section-title h2:after {
  content: '';
  height: 1px;
  width: 110px;
  background-color: #bbbbbb;
  display: block;
  margin-bottom: 10px;
  margin-top: 20px; }

.text-grey {
  color: #dcdcdc; }

.col-wrapper {
  padding: 14px; }

.col-wrapper:hover {
  background-color: #f9f9f9; }

.col-content {
  text-align: center; }

.col-content h4 {
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0;
  color: #222222; }

.col-content p {
  color: #444;
  line-height: 26px; }

.col-content i {
  font-size: 2em;
  color: #888;
  margin-bottom: .5rem; }

.col-content:not(.service) i {
  background-color: #f5f5f5;
  font-size: 30px;
  width: 70px;
  height: 70px;
  line-height: 70px;
  border-radius: 50%;
  margin-bottom: 20px;
  color: #212121; }

.progress-title {
  font-weight: normal;
  font-size: 15px;
  letter-spacing: 1.5px;
  color: #888;
  line-height: 30px;
  -webkit-font-smoothing: antialiased;
  font-family: 'Poppins', sans-serif; }
  .progress-title span {
    color: #000; }

.custom-progress {
  font-size: .75rem;
  background-color: #e9ecef;
  border-radius: .1rem !important;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);
  margin-bottom: 10px;
  height: 7px !important; }

.progress-bar-black {
  background-color: #000 !important; }

.price-table {
  margin: 40px 0; }

.price-box {
  max-width: 340px;
  margin: 0 auto;
  padding: 40px;
  background-color: #fff;
  text-align: center;
  border: 1px solid #dcdbdb; }
  .price-box.shadow {
    box-shadow: 0 0 20px 8px #e4e2e2; }
  .price-box h5 {
    font-size: 16px;
    line-height: 24px;
    margin: 0;
    font-family: 'Russo One', sans-serif;
    font-weight: 700;
    color: #000;
