  .price-box .price-features p {
    margin-bottom: 20px;
    color: #888 !important; }
  .price-box .lead {
    font-size: 20px;
    font-weight: 300;
    line-height: 1.4; }
  .price-box p {
    font-family: 'Poppins', sans-serif;
    font-size: 14px;
    font-weight: normal;
    letter-spacing: 0;
    line-height: 28px;
    margin: 0; }

.btn-appear {
  border: 0px;
  border-radius: 0px !important;
  padding: 10px 20px;
  letter-spacing: 0.2em;
  background-color: #222 !important; }

a, a:visited {
  text-decoration: none;
  font-family: 'Poppins', sans-serif;
  font-weight: normal;
  font-size: 12px !important;
  color: #fff !important;
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  outline: 0; }

.quote {
  text-align: center;
  color: #888;
  margin-bottom: 30px; }
  .quote i {
    font-size: 4em; }

.profile-item {
  margin: 0 auto;
  width: 150px; }
  .profile-item img {
    border-radius: 50%; }
  .profile-item span {
    text-align: center;
    font-weight: bold;
    color: #d6d6d6;
    display: block;
    margin-top: 20px; }

blockquote {
  border-left: 0 none;
  font-family: 'Poppins', sans-serif;
  margin: 15px 0 !important;
  position: relative; }
  blockquote p {
    color: #888;
    font-style: italic;
    padding: 0;
    font-size: 18px;
    line-height: 36px;
    margin-bottom: 30px; }

.carousel-indicators {
  position: absolute;
  right: 0;
  bottom: -20px !important;
  left: 0;
  z-index: 15;
  display: flex;
  justify-content: center;
  padding-left: 0;
  margin-right: 15%;
  margin-left: 15%;
  list-style: none; }
  .carousel-indicators li {
    background-color: #888; }

.testimonials {
  overflow: hidden;
  padding: 14px; }

footer {
  padding: 14px;
  text-align: center;
  position: relative;
  color: #888;
  font-family: 'Poppins', sans-serif;
  font-weight: normal;
  font-size: 15px;
  letter-spacing: 1.5px;
  color: #888;
  line-height: 30px; }

.main-footer {
  padding: 50px 15px 0; }

.social-icon span {
  width: 30px;
  height: 30px;
  line-height: 30px;
  border-radius: 50%;
  margin: 0 1px;
  background: #2f2f2f;
  -webkit-transition: all .3s;
  transition: all .3s;
  display: inline-block; }

.scrollTop a {
  color: #fff;
  position: absolute;
  left: calc(50% - 35px);
  top: -35px;
  background: #222;
  height: 70px;
  width: 70px;
  border-radius: 50%;
  text-align: center;
  line-height: 70px;
  box-shadow: 0px -2px 9.9px 0.1px rgba(186, 187, 188, 0.25);
  font-size: 27px !important;
  z-index: 11; }

#columns {
  -webkit-column-width: 320px;
     -moz-column-width: 320px;
          column-width: 320px;
  grid-column-gap: 0px;
  -webkit-column-gap: 0px;
     -moz-column-gap: 0px;
          column-gap: 0px;
  width: 100%;
  margin: 50px auto; }

#columns figure {
  background: #fefefe;
  width: 100%;
  margin: 0 2px 15px;
  -webkit-transition: opacity .4s ease-in-out;
  transition: opacity .4s ease-in-out;
  display: inline-block;
  page-break-inside: avoid;
  -webkit-column-break-inside: avoid;
     -moz-column-break-inside: avoid;
          break-inside: avoid;
  box-shadow: 0 1px 2px rgba(34, 25, 25, 0.4); }

#columns figure img {
  width: 100%;
  height: auto;
  margin-bottom: 5px; }

#columns figure figcaption {
  font-size: .9rem;
  color: #444;
  line-height: 1.5;
  padding: 15px; }

#columns small {
  font-size: 1rem;
  float: right;
  text-transform: uppercase;
  color: #aaa; }

#columns small a {
  color: #666;
  text-decoration: none;
  -webkit-transition: .4s color;
  transition: .4s color; }

#columns:hover figure:not(:hover) {
  opacity: 0.4; }

@media screen and (min-width: 768px) {
  #columns {
    grid-column-gap: 15px;
    -webkit-column-gap: 15px;
       -moz-column-gap: 15px;
            column-gap: 15px; }
  .divider {
    display: block; } }

